
	  
	  
import React from 'react';
import ArticleListPage from '../components/ArticleListPage';
import ArticleContent from './ArticleContent';

const ArticleList = () => (
    <div>
    <h1>Articles</h1>
    <ArticleListPage articles={ArticleContent} />
    </div>
);

export default ArticleList;


